<template>
  <div style="padding:15px 0;position:fixed;top:50px;bottom:0;left:0;right:0;overflow-y:scroll;background-color:#F6F6F6;font-size:0.8rem;">
    <div id="status_box">
      <span style="display:inline-block;width:30%;-border:1px solid red;text-align:left;">账单日</span>
      <span style="display:inline-block;width:8%;-border:1px solid red;">状态</span>
      <span style="display:inline-block;width:30%;-border:1px solid red;text-align:right;">金额</span>
    </div>
    <!-- 月份 -->
    <div :key="contentKey">
      <div
        v-for="(item,index) in selected==1?listArr:listArr.filter((item,i)=>i<5)"
        :key="index"
      >
        <div
          class="all"
          @click="toggle(item)"
        >
          <span style="margin-left:20px;">{{ item.y }}年{{ item.m }}月</span>
          <span v-show="item.statu==false">
            <img
              style="width:20px;height:20px;vertical-align:middle;"
              src="@/assets/right.png"
              alt
            />
          </span>
          <span v-show="item.statu==true">
            <img
              style="width:20px;height:20px;vertical-align:middle;"
              src="@/assets/bottom.png"
              alt
            />
          </span>
          <span
            class="money"
            v-if="priceAll[item.y+'-'+item.m]"
          >
            <span style="display:inline-block;-width:122px;text-align:left;display: flex;">
              总收入&ensp;&yen;
              <span style="color:#888;">{{ priceAll[item.y+'-'+item.m] }}</span>
            </span>&emsp;
          </span>
        </div>
        <div>
          <ul
            v-show="item.statu"
            :key="key"
            style="margin-bottom:10px;"
          >
            <li
              class="other_box"
              v-if="currentMounth == timeaaa && outBill >0"
            >
              <span
                class="li-box"
                style="color:#FF7D41;"
                @click="goNotPaid({type:'notPaid'})"
              >
                <span style="display:inline-block;width:32%;margin-left: 1rem;"></span>
                <span style="display:inline-block;width:30%;">未出账</span>
                <span style="display:inline-block;width:30%;">&yen;{{ outBill }}</span>
                <img
                  style="width:20px;height:20px;padding:8px 7px 0px 0px;color:#000;"
                  src="@/assets/right.png"
                  alt
                />&nbsp;
              </span>
            </li>
            <li
              v-for="(item_o,index) in options[item.y+'-'+item.m]"
              :key="index"
              class="other_box"
              @click="goPaid(item_o,{type:'Paid'})"
            >
              <span class="li-box">
                <span style="display:inline-block;width:32%;margin-left: 1rem;">{{ item_o.billStartTime }}</span>
                <span style="display:inline-block;width:30%;color:#000;">已出账</span>
                <span style="display:inline-block;width:30%;color:#000;">&yen;{{ item_o.totalPrice }}</span>
                <img
                  style="width:20px;height:20px;padding:8px 7px 0px 0px;color:#000;"
                  src="@/assets/right.png"
                  alt
                />&nbsp;
              </span>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { GetBusinessBillRecordAll } from '@/api/api_business_hotel_and_scale';
export default {
    components: {},
    data() {
        return {
            key: 0,
            listArr: [],
            item: '',
            statu: false,
            options: {},
            s: '',
            priceAll: {},
            refuseAll: {},
            selected: '1',
            contentKey: 0,
            day: 14,
            timeaaa: null,
            currentMounth: null,
            outBill: null,
            num: '1'
        };
    },
    created() {
        this.$store.commit('setHeader', '平台账单');
        let current_time = new Date();
        let year = current_time.getFullYear();
        let mouth = current_time.getMonth() + 1 < 10 ? '0' + (current_time.getMonth() + 1) : current_time.getMonth() + 1;
        this.currentMounth = year + '-' + mouth;
        for (let i = 0; i < this.day; i++) {
            let tempMouth = mouth - i;
            let tempYear = year;
            if (tempMouth <= 0) {
                let abstempMouth = Math.abs(tempMouth);
                tempMouth = 12 - (abstempMouth % 12);
                let tempYJ = parseInt(abstempMouth / 12) + 1;
                tempYear = tempYear - tempYJ;
            }
            this.listArr.push({
                y: tempYear,
                m: tempMouth < 10 ? '0' + tempMouth : tempMouth,
                statu: i == 0 ? true : false
            });
        }
        this.toggle(this.listArr[0], 1);
    },
    methods: {
        // 切换
        btnSearch(num) {
            this.num = num;
            this.priceAll = {};
            this.listArr.forEach((item) => {
                item.statu = false;
            });
            this.options = [];
            this.toggle(this.listArr[0], 1);
        },
        // 切换滑块
        toggle(item, first) {
            this.item = item;
            if (first == 1) {
                item.statu = true;
            } else {
                item.statu = !item.statu;
            }
            if (item.statu == true) {
                this.getprice(item.y, item.m);
            }
        },
        // 日租
        getprice(y, m) {
            let data = {
                billTime: this.item.y + '-' + this.item.m
            };
            this.timeaaa = data.billTime;
            GetBusinessBillRecordAll(data).done((res) => {
                this.outBill = res.order;
                this.options[y + '-' + m] = [];
                this.options[y + '-' + m] = res.data;
                this.func(this.options[y + '-' + m], y + '-' + m);
                ++this.contentKey;
            });
        },
        func(options, ym) {
            let priceAll = 0;
            let refuseAll = 0;
            for (let i = 0; i < options.length; i++) {
                if (this.num != 2) {
                    priceAll += options[i].totalPrice;
                    this.priceAll[ym] = priceAll.toFixed(2);
                    refuseAll += options[i].refuseAmount;
                    this.refuseAll[ym] = refuseAll.toFixed(2);
                    ++this.key;
                } else {
                    priceAll += options[i].totalPrice;
                    this.priceAll[ym] = priceAll.toFixed(2);
                    ++this.key;
                }
            }
        },
        // 未出账
        goNotPaid(obj) {
            this.$router.push({
                name: 'OutRecord',
                query: {
                    type: obj.type,
                    isshow: '0'
                }
            });
        },
        // 已出账
        goPaid(item, obj) {
            if (this.selected == 1) {
                let Billdata = {
                    totalPrice: item.totalPrice,
                    roomAmount: item.roomAmount,
                    depositAmount: item.depositAmount,
                    extraAmount: item.extraAmount,
                    reverseAmount: item.reverseAmount,
                    refuseAmount: item.refuseAmount,
                    aiyoBalanceAmount: item.aiyoBalanceAmount,
                    wxBalanceAmount: item.wxBalanceAmount,
                    aliBalanceAmount: item.aliBalanceAmount
                };
                this.$router.push({
                    name: 'OutRecord',
                    query: {
                        billRecordId: item.id,
                        type: obj.type,
                        billStartTime: item.billStartTime,
                        isshow: '0',
                        selected: '1',
                        Billdata
                    }
                });
            } else {
                item.selected = this.selected;
                this.$router.push({
                    name: 'OutRecord',
                    query: {
                        billRecordId: item.id,
                        list: item
                    }
                });
                localStorage.setItem('list', JSON.stringify(item));
            }
        }
    }
};
</script>

<style scoped>
ul {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
#status_box {
    line-height: 50px;
    background-color: #fff;
}
.all {
    display: flex;
    justify-content: space-between;
    padding: 0px 10px 10px 10px;
    background-color: #fff;
    line-height: 40px;
    position: relative;
}
.money {
    font-size: 14px;
    position: absolute;
    top: 18px;
    left: 28px;
    display: flex;
}
.li-box {
    width: 100%;
    font-size: 0.8rem;
    display: flex;
    justify-content: space-between;
    background-color: #fff;
}
.other_box {
    color: #888888;
    line-height: 2rem;
    border-bottom: 1px solid #f6f6f6;
}
select {
    border: solid 1px #000;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    border: none;
    outline: none;
}
select::-ms-expand {
    display: none;
}
.mint-navbar .mint-tab-item.is-selected {
    color: #ff7d41;
    margin-bottom: 0;
    border-bottom: 3px solid #ff7d41;
}
</style>



